<template>
    <div class="first-layout">
        <div id="PrincipleContent" class="default-text-content">
            <p>
                对于二元物系，如已知其汽液平衡数据，则根据精馏塔的原料液组成，进料热状况，操作回流比及塔顶馏出液组成，塔底釜液组成可以求出该塔的理论板数NT，按照式1可以得到总板效率ET，其中NP为实际塔板数。
            </p>
            <div class="flex-center">
                <math displaystyle="true">
                    <msub>
                        <mi>E</mi>
                        <mn>T</mn>
                    </msub>
                    <mo>=</mo>
                    <mfrac>
                        <msub>
                            <mi>N</mi>
                            <mn>T</mn>
                        </msub>
                        <msub>
                            <mi>N</mi>
                            <mn>P</mn>
                        </msub>
                    </mfrac>
                    <mo>&times</mo>
                    <mo>100%</mo>
                </math>
            </div>
            <div class="flex-center" style="justify-content: right">
                <strong>(1)</strong>
            </div>
            <p>
                在全回流条件下，只要测得塔顶馏出液组成和塔底组成，即可根据双组份物系的相平衡关系，在图上通过图解法求得理论板数；而塔内实际板数已知，根据上式可求得全塔效率。
            </p>
            <p>
                在部分回流条件下，理论板数NT
                由已知的双组分物系平衡关系，通过实验测得塔顶产品组成xD、进料组成xF、回流比R、进料温度
                tF
                等得出精馏段操作线方程及q线方程，根据塔釜组成xW确定提馏段操作线方程，利用图解法计算求得理论板数，进而求得全塔效率。
            </p>
            <p>
                <strong>部分回流时，进料热状况参数的计算式为:</strong>
                <br>
            </p>
            <div class="flex-center">
                <math displaystyle="true">
                    <mi>q</mi>
                    <mo>=</mo>
                    <mfrac>
                        <mrow>
                            <msub>
                                <mi>C</mi>
                                <mn>pm</mn>
                            </msub>
                            <mo>(</mo>
                            <msub>
                                <mi>t</mi>
                                <mn>B</mn>
                            </msub>
                            <mi>P</mi>
                            <mo>-</mo>
                            <msub>
                                <mi>t</mi>
                                <mn>F</mn>
                            </msub>
                            <mo>)</mo>
                            <mo>+</mo>
                            <msub>
                                <mi>r</mi>
                                <mn>m</mn>
                            </msub>
                        </mrow>
                        <msub>
                            <mi>r</mi>
                            <mn>m</mn>
                        </msub>
                    </mfrac>
                </math>
            </div>
            <div class="flex-center" style="justify-content: right">
                <strong>(2)</strong>
            </div>
            <h3>式中:<br></h3>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>t</mi>
                            <mn>F</mn>
                        </msub>
                    </mrow>
                </math>
                — 进料温度，℃ ；
            </p>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>t</mi>
                            <mn>BP</mn>
                        </msub>
                    </mrow>
                </math>
                — 进料的泡点温度，℃ ；
            </p>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>C</mi>
                            <mn>pm</mn>
                        </msub>
                    </mrow>
                </math>
                —进料液体在平均温度
                <math displaystyle="true">
                    <mrow>
                        <mfrac>
                            <mrow>
                                <mo>(</mo>
                                <msub>
                                    <mi>t</mi>
                                    <mn>F</mn>
                                </msub>
                                <mo>+</mo>
                                <msub>
                                    <mi>t</mi>
                                    <mn>P</mn>
                                </msub>
                                <mo>)</mo>
                            </mrow>
                            <mi>2</mi>
                        </mfrac>
                    </mrow>
                </math>
                下的比热，
                <math>
                    <mrow>
                        <mi>kJ/(kmol·℃)</mi>
                    </mrow>
                </math>
                ;
            </p>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>r</mi>
                            <mn>m</mn>
                        </msub>
                    </mrow>
                </math>
                —进料液体在其组成和泡点温度下的汽化潜热，
                <math>
                    <mrow>
                        <mi>kJ/kmol</mi>
                    </mrow>
                </math>
                。
            </p>
            <div class="flex-center">
                <math displaystyle="true">
                    <msub>
                        <mi>C</mi>
                        <mn>pm</mn>
                    </msub>
                    <mo>=</mo>
                    <mrow>
                        <msub>
                            <mi>C</mi>
                            <mn>p1</mn>
                        </msub>
                        <msub>
                            <mi>M</mi>
                            <mn>1</mn>
                        </msub>
                        <msub>
                            <mi>X</mi>
                            <mn>1</mn>
                        </msub>
                    </mrow>
                    <mo>+</mo>
                    <mrow>
                        <msub>
                            <mi>C</mi>
                            <mn>p2</mn>
                        </msub>
                        <msub>
                            <mi>M</mi>
                            <mn>2</mn>
                        </msub>
                        <msub>
                            <mi>X</mi>
                            <mn>2</mn>
                        </msub>
                    </mrow>
                </math>
            </div>
            <div class="flex-center" style="justify-content: right">
                <strong>(3)</strong>
            </div>
            <br>
            <div class="flex-center">
                <math displaystyle="true">
                    <msub>
                        <mi>r</mi>
                        <mn>m</mn>
                    </msub>
                    <mo>=</mo>
                    <mrow>
                        <msub>
                            <mi>r</mi>
                            <mn>1</mn>
                        </msub>
                        <msub>
                            <mi>M</mi>
                            <mn>1</mn>
                        </msub>
                        <msub>
                            <mi>X</mi>
                            <mn>1</mn>
                        </msub>
                    </mrow>
                    <mo>+</mo>
                    <mrow>
                        <msub>
                            <mi>r</mi>
                            <mn>2</mn>
                        </msub>
                        <msub>
                            <mi>M</mi>
                            <mn>2</mn>
                        </msub>
                        <msub>
                            <mi>X</mi>
                            <mn>2</mn>
                        </msub>
                    </mrow>
                </math>
            </div>
            <div class="flex-center" style="justify-content: right">
                <strong>(4)</strong>
            </div>
            <h3>式中:<br></h3>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>C</mi>
                            <mn>P1</mn>
                        </msub>
                        <msub>
                            <mi>C</mi>
                            <mn>P2</mn>
                        </msub>
                    </mrow>
                </math>
                —分别为纯组份1和组份2在平均温度下的比热，
                <math>
                    <mrow>
                        <mi>kJ/(kg·℃)</mi>
                    </mrow>
                </math>
                ;
            </p>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>r</mi>
                            <mn>1</mn>
                        </msub>
                        <msub>
                            <mi>r</mi>
                            <mn>2</mn>
                        </msub>
                    </mrow>
                </math>
                —分别为纯组份1和组份2在泡点温度下的汽化潜热，
                <math>
                    <mrow>
                        <mi>kJ/kg</mi>
                    </mrow>
                </math>
                ;
            </p>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>M</mi>
                            <mn>1</mn>
                        </msub>
                        <msub>
                            <mi>M</mi>
                            <mn>2</mn>
                        </msub>
                    </mrow>
                </math>
                —分别为纯组份1和组份2的摩尔质量，
                <math>
                    <mrow>
                        <mi>kJ/kmol</mi>
                    </mrow>
                </math>
                ;
            </p>
            <p>
                <math>
                    <mrow>
                        <msub>
                            <mi>x</mi>
                            <mn>1</mn>
                        </msub>
                        <msub>
                            <mi>x</mi>
                            <mn>2</mn>
                        </msub>
                    </mrow>
                </math>
                —分别为纯组份1和组份2在进料中的摩尔分率。
            </p>
        </div>
    </div>
</template>


<script>
//实验原理
export default {
    name: "Principle",
    mounted() {
        if (this.MathJax.isMathjaxConfig) {
            this.MathJax.initMathjaxConfig();
        }
        //这个地方的hello是对应要渲染数学公式的dom的class
        this.MathJax.MathQueue("PrincipleContent");
    }
};
</script>

